import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Platform, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-media',
  templateUrl: './media.page.html',
  styleUrls: ['./media.page.scss'],
  providers: [ApiService]
})
export class MediaPage implements OnInit {

  subsCategory: any;
  catecories = [];

  constructor(
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    private router: Router
  ) { }

  ngOnInit() {
    this.getListCategory();
  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Akun anda terdeteksi sedang digunakan diperangkat lain, mohon login kembali untuk mendapatkan akses Anda kembali',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {

            localStorage.removeItem('currentUser');
            localStorage.removeItem('geo_id');
            localStorage.removeItem('api_key');
            this.router.navigate(['front/']);

          }
        }
      ]
    });

    await alert.present();
  }


  getListCategory() {

    this.catecories = [];
    this.subsCategory = this.apiService.get('medias?summary=1', {}).subscribe(
      data => {
        this.catecories = data;
      }, error => {
        if (error.status == 401) {
          this.alertToRelogin();
        }
      }
    );

  }

  actionSubCategory(params) {
    // alert(id);
    this.NavController.navigateForward(['/home/media/category', params]);
  }

  goBack() {
    this.NavController.navigateBack(['/home'], { replaceUrl: true });
  }

}
