import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Platform, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.page.html',
  styleUrls: ['./category.page.scss'],
  providers: [ApiService]
})
export class CategoryPage implements OnInit {

  params: any;
  subsCategory: any;
  Catecories = [];

  constructor(
    private apiService: ApiService,
    private route: ActivatedRoute,
    public NavController: NavController,
  ) {
    this.params = this.route.snapshot.params;
    // console.log(this.params);
  }

  ngOnInit() {
    this.getListCategory();
  }

  getListCategory() {

    this.Catecories = [];
    this.subsCategory = this.apiService.get('medias?summary=1&category_id=' + this.params.id, {}).subscribe(
      data => {
        // console.log(data);
        this.Catecories = data;
      }
    );

  }

  getDetailSubCategory(category) {

    var newParams = {
      category_id: this.params.id,
      category_name: this.params.name,
      subcategory_id: category.id,
      subcategory_name: category.name,
    }

    this.NavController.navigateForward(['/home/media/list', newParams]);

  }

  goBack() {
    this.NavController.navigateBack(['/home/media'], { replaceUrl: true });
  }

}
