import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Platform, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { async } from '@angular/core/testing';


@Component({
  selector: 'app-list',
  templateUrl: './list.page.html',
  styleUrls: ['./list.page.scss'],
  providers: [ApiService]
})
export class ListPage implements OnInit {

  params: any;
  subscribe: any;
  medias = [];

  text: string = 'Flamenco';
  imgurl: string = 'https://cdn.movvillagetaskforce.com/media/files/Apa_Itu_COVID.jpg';
  link: string = 'https://link.medium.com/JA4amAHFJ5';

  constructor(
    private apiService: ApiService,
    private route: ActivatedRoute,
    public NavController: NavController,
    private InAppBrowser: InAppBrowser,
    public alertController: AlertController,
    public actionSheetController: ActionSheetController,
    private socialSharing: SocialSharing
  ) {
    this.params = this.route.snapshot.params;
    console.log(this.params);
  }

  ngOnInit() {
    this.getListMedia();
  }

  getListMedia() {

    this.medias = [];
    this.subscribe = this.apiService.get('medias?category_id=' +
      this.params.category_id + '&subcategory_id=' + this.params.subcategory_id, {}).subscribe(
        data => {
          // console.log(data);
          this.medias = data;
        }
      );

  }

  getDetailMedia(medias) {

    var newParams = {
      category_id: this.params.category_id,
      category_name: this.params.category_name,
      subcategory_id: this.params.subcategory_id,
      subcategory_name: this.params.subcategory_name,
      media_id: medias.id,
      media_url: medias.url,
      media_name: medias.name
    }

    let extension = this.checkExtention(medias.url);

    if (extension == 'pdf') {

      let framePDF = 'https://docs.google.com/viewerng/viewer?url=' + medias.url + '&embedded=true';
      this.InAppBrowser.create(framePDF, '_system');

      // this.NavController.navigateForward(['/home/media/showing', newParams]);

    } else if (extension == 'img') {
      this.NavController.navigateForward(['/home/media/showing', newParams]);
    } else if (extension == 'mp4') {
      this.NavController.navigateForward(['/home/media/showing', newParams]);
    } else {
      alert(extension);
    }

  }

  async presentActionSheetShareWith(medias, extension) {

    let buttons = [];

    if (extension == 'img') {
      buttons.push({
        text: 'Whatsapp',
        handler: () => {
          this.ShareWhatsapp(medias);
        }
      }, {
        text: 'Facebook',
        handler: () => {
          this.ShareFacebook(medias);
        }
      }, {
        text: 'Instagram',
        handler: () => {
          this.SendInstagram(medias);
        }
      }, {
        text: 'Twitter',
        handler: () => {
          this.SendTwitter(medias);
        }
      });

    } else if (extension == 'pdf') {

      buttons.push({
        text: 'Whatsapp',
        handler: () => {
          this.ShareWhatsapp(medias);
        }
      });

    }

    buttons.push({
      text: 'Share Link',
      handler: () => {
        this.sharingMedia(medias);
      }
    }, {
      text: 'Tutup',
      role: 'cancel',
      handler: () => {
      }
    });

    const actionSheet = await this.actionSheetController.create({
      header: medias.name,
      cssClass: 'my-custom-class',
      mode: 'ios',
      buttons: buttons
    });
    await actionSheet.present();
  }

  async presentActionSheetOptionMedia(medias) {

    const actionSheet = await this.actionSheetController.create({
      header: medias.name,
      cssClass: 'my-custom-class',
      mode: 'ios',
      buttons: [{
        text: 'Buka',
        handler: () => {
          this.getDetailMedia(medias);
        }
      }, {
        text: 'Bagikan',
        handler: async () => {

          let extension = await this.checkFileExtention(medias.url);

          this.presentActionSheetShareWith(medias, extension);
        }
      },
      //  {
      //   text: 'Download',
      //   handler: () => {
      //     console.log('Play clicked');
      //   }
      // },
      {
        text: 'Tutup',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }]
    });
    await actionSheet.present();
  }

  checkExtention(media_url: string) {
    let split_media = media_url.split('.');
    let image_extension = ['jpg', 'jpeg', 'png'];

    let extension = (image_extension.includes((split_media[split_media.length - 1])))
      ? 'img' : split_media[split_media.length - 1];

    return extension;
  }

  async checkFileExtention(media_url: string): Promise<any> {
    return new Promise(async (resolve, rejected) => {

      try {

        let split_media = media_url.split('.');
        let image_extension = ['jpg', 'jpeg', 'png'];

        let extension = (image_extension.includes((split_media[split_media.length - 1])))
          ? 'img' : split_media[split_media.length - 1];

        resolve(extension);

      } catch (Error) {
        rejected(Error)
      }

    });

  }

  sharingMedia(medias: any) {

    var options = {
      message: medias.name, // not supported on some apps (Facebook, Instagram)
      url: medias.url,
    };

    this.socialSharing.shareWithOptions(options);

  }

  ShareWhatsapp(medias) {
    // this.text, this.imgurl, this.link
    this.socialSharing.shareViaWhatsApp(medias.name, medias.url, medias.url)
  }

  ShareFacebook(medias) {
    this.socialSharing.shareViaFacebookWithPasteMessageHint(medias.name, medias.url, null /* url */, 'Berhasil dibagikan!')
  }

  SendInstagram(medias) {
    this.socialSharing.shareViaInstagram(medias.name, medias.url)
  }

  SendTwitter(medias) {
    //(this.text, this.imgurl, this.link)
    this.socialSharing.shareViaTwitter(medias.name, medias.url, medias.url)
  }

  goBack() {

    var params = {
      id: this.params.category_id,
      name: this.params.category_name
    };

    // , { replaceUrl: true }
    this.NavController.navigateBack(['/home/media/category', params]);

  }

}
